import React, { Component } from "react"
import {parseClass} from "../../../utils"

class ImageSlider extends Component {
    constructor(props) {
        super(props)

        this.state = {
            currentIndex: 0,
        }
        this.handlerNextImage = this.handlerNextImage.bind(this)
        this.autoPlay = this.autoPlay.bind(this)
        this.stopAutoPlay = this.stopAutoPlay.bind(this)
        this.handleClickPoint = this.handleClickPoint.bind(this)
        this.changeTheIndexOfImg = this.changeTheIndexOfImg.bind(this)
    }

    componentDidMount() {
        this.autoPlay()
    }

    componentWillUnmount() {
        this.stopAutoPlay()
    }

    changeTheIndexOfImg(index) {
        this.setState({
            currentIndex: index,
        })
    }

    playNextImage(step) {
        let lengthOfImages = this.props.images.length
        let indexOfNextImg = (this.state.currentIndex + step + lengthOfImages) % lengthOfImages
        this.changeTheIndexOfImg(indexOfNextImg)
    }

    handlerNextImage(event) {
        let step = Number(event.target.dataset.step)
        this.playNextImage(step)
    }

    autoPlay() {
        this.timerID = setInterval(() => {
            this.playNextImage(1)
        }, 5000)
    }

    stopAutoPlay() {
        clearInterval(this.timerID)
    }

    handleClickPoint(event) {
        let index = Number(event.target.dataset.index)
        this.changeTheIndexOfImg(index)
    }

    render() {
        let images = this.props.images
        let mode = this.props.mode
        return(
            <div className='image-slider' onMouseOver={this.stopAutoPlay} onMouseOut={this.autoPlay}>
                <button className='slide-button slide-left vertical-center'
                        onClick={this.handlerNextImage}
                        data-step="-1"
                >
                    &lt;
                </button>
                {this[mode]()}
                <button className='slide-button slide-right vertical-center'
                        onClick={this.handlerNextImage}
                        data-step="1"
                >
                    &gt;
                </button>
                <div className='slide-point-container'>
                    {
                        images.map((value, index) => {
                            let classNameOfPoint = parseClass({
                                'slide-point': true,
                                'active-point': index === this.state.currentIndex,
                            })
                            return (
                                <span className={classNameOfPoint}
                                  key={index}
                                  data-index={index}
                                  onClick={this.handleClickPoint}
                                > </span>
                            )
                        })
                    }
                </div>
            </div>
        )
    }

    smooth() {
        let {widthOfImg, heightOfSlider, images} = this.props
        let widthOfSlider = images.length * widthOfImg
        let offset = -(this.state.currentIndex % widthOfSlider) * widthOfImg || 0
        return (
            <div className='image-slider-container'
                 style={{
                     width: widthOfSlider,
                     height: heightOfSlider,
                     transform: `translate(${offset}px, 0px)`
                 }}
            >
                {
                    images.map((value, index) => (
                        <img key={index} src={value.picUrl} alt="轮播图" />
                    ))
                }
            </div>
        )
    }

    carousel() {
        let {widthOfImg, heightOfSlider, images} = this.props
        let currentIndex = this.state.currentIndex
        let length = images.length
        let mapIndexOffsetToStyle = {
            '-1': {
                offset: -widthOfImg / 3,
                zIndex: 1,
                scale: 0.9,
            },
            '0': {
                offset: 0,
                zIndex: 3,
                scale: 1,
            },
            '1': {
                offset: widthOfImg / 3,
                zIndex: 1,
                scale: 0.9,
            },
            other: {
                offset: 0,
                zIndex: 0,
                scale: 0.9,
            }
        }
        return (
            <div className='image-slider-container horizontal-center'
                 style={{
                     width: widthOfImg,
                     height: heightOfSlider,
                 }}
            >
                {
                    images.map((value, index) => {
                        let indexOffset = index - currentIndex
                        if (indexOffset === length - 1) {
                            indexOffset = -1
                        } else if (indexOffset === 1 - length) {
                            indexOffset = 1
                        }
                        let style = mapIndexOffsetToStyle[indexOffset]
                        if (style === undefined) {
                            style = mapIndexOffsetToStyle['other']
                        }
                        let {offset, scale, zIndex} = style
                        let styleOfImg = {
                            position: 'absolute',
                            // float: 'left',
                            transform: `translate(${offset}px, 0px) scale(${scale})`,
                            zIndex: zIndex,
                        }
                        return (
                            <img key={index} src={value.picUrl} alt="轮播图" style={styleOfImg} />
                        )
                    })
                }
            </div>
        )
    }
}

export default ImageSlider