import React, { Component } from 'react'
import ImageSlider from "./ImageSlider"
import {connect} from "react-redux"
import {fetchImgInHomePage} from "../actions"
import './Home.css'

class Home extends Component {
    componentDidMount() {
        console.log('this.props.images.length', this.props.images)
        if (this.props.images.length === 0) {
            this.props.fetchImgInHomePage()
        }
    }

    render() {
        return (
            <div>
                <ImageSlider
                    {...{
                        images: this.props.images,
                        fetchImgInHomePage: this.props.fetchImgInHomePage,
                        mode: 'carousel',
                        widthOfImg: 730,
                        heightOfSlider: 336,
                    }}
                />
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    let theState = state.homePageReducer
    return {
        images: theState.images,
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        fetchImgInHomePage: () => {
            dispatch(fetchImgInHomePage())
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Home)
